import axios from 'axios';

const AxiosPlugin = {
    install(Vue) {
        Vue.prototype.$axios = axios.create({
            baseURL: `${process.env.VUE_APP_API_URL}/api`,
            headers: {
                'Access-Control-Allow-Origin' : '*',
                'X-Requested-With' : 'XMLHttpRequest'
            },
            withCredentials: true,
            crossDomain: true
        });
    }
};

export default (AxiosPlugin);
